<template>
  <div
    class="page-box page-break-before"
    v-if="currentRoom"
  >
    <div class="page-box-title">
      <h3>
        Akoestische analyse &amp; advies
      </h3>
      <div class="flex-divider"></div>
      <base-button
        type="secondary"
        @click="printAdvice()"
      >
        <base-icon iconRef="printer" />
      </base-button>
      <base-button
        type="secondary"
        @click="openCorrectionPrompt()"
      >
        <base-icon iconRef="options" />
      </base-button>
      <!--
      <input
        class="input xs correction"
        type="number"
        v-model="correction"
      -->
    </div>
    <div class="page-box-content">
      <div class="measurements-chart-wrapper">
        <measurements-chart
          :data="[rt60ContextualAdviceValues, rt60ZeroMeasurement, rt60WithProducts]"
          :scaleMaxHeight="chartMax"
          :height="250"
          :updateOnValueChange="correction"
        ></measurements-chart>
        <acousticsscore-chart
          :acousticsScores="acousticsScores"
          :currentScenarioHasNoProducts="currentScenarioHasNoProducts"
        ></acousticsscore-chart>
      </div>
      <div class="advice-ribbon">
        <div class="ribbon-orbs">
          <div class="ribbon-orb-group">
            <div class="ribbon-orb-group-label label">
              Advieswaarde
            </div>
            <div class="ribbon-orb-group-orbs">
              <div class="ribbon-orb advice">
                <div>
                  <span class="ribbon-value">
                    <base-counter :end="getWeightedAverage(rt60ContextualAdviceValues)" />
                  </span> s
                </div>
              </div>
            </div>
          </div>
          <div class="ribbon-orb-group">
            <div class="ribbon-orb-group-label label">
              Huidige akoestiek
            </div>
            <div class="ribbon-orb-group-orbs">
              <div class="ribbon-orb current">
                <div>
                  <span class="ribbon-value">
                    <base-counter :end="getWeightedAverage(rt60ZeroMeasurement)" />
                  </span>
                  <span class="ribbon-sub-label">s</span>
                </div>
              </div>
              <div class="ribbon-orb current">
                <div>
                  <span class="ribbon-label color-red-dark">
                    <base-counter
                      :end="acousticsScores[0]"
                      :decimals="1"
                    />
                  </span><span class="ribbon-sub-label"> / <span class="color-green-dark">10</span></span></div>
              </div>
            </div>
          </div>
          <div
            class="ribbon-orb-group"
            v-show="!currentScenarioHasNoProducts"
          >
            <div class="ribbon-orb-group-label label">
              Na toepassen producten
            </div>
            <div class="ribbon-orb-group-orbs">
              <div class="ribbon-orb with-products">
                <div>
                  <span class="ribbon-value">
                    <base-counter :end="getWeightedAverage(rt60WithProducts)" />
                  </span> s
                </div>
              </div>
              <div
                class="ribbon-orb with-products"
                v-show="!currentScenarioHasNoProducts"
              >
                <div>
                  <span class="ribbon-label color-primary-dark">
                    <base-counter
                      :end="acousticsScores[1]"
                      :decimals="1"
                    />
                  </span><span class="ribbon-sub-label"> / <span class="color-green-dark">10</span></span></div>
              </div>
              <div
                class="ribbon-orb with-products"
                v-show="!currentScenarioHasNoProducts"
              >
                <div>
                  <span class="ribbon-value">
                    <base-counter
                      :end="currentScenarioProductsTotalArea"
                      :decimals="2"
                    />
                  </span>
                  <span class="ribbon-sub-label">m<sup>2</sup></span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <p class="disclaimer show-only-on-print">De calculaties zijn gebaseerd op akoestische modellen en schattingen voor algemene situaties. Eventuele metingen zijn ad-hoc uitgevoerd, zonder dat alle bouwkundige eigenschappen bekend zijn. De meetresultaten en calculaties zijn bedoeld ter indicatie; er kunnen geen rechten aan worden ontleend.</p>
      <div class="flex-row">
        <div class="flex-divider"></div>
        <p
          class="advice-changed-disclaimer"
          v-show="adviceChanged"
        >
          <base-icon
            iconRef="alert-circle"
            position="left"
          />Vergeet niet om het advies op te slaan.
        </p>
        <base-button
          :loading="savingAdvice"
          :disabled="savingAdvice"
          @click="saveAdvice"
        >
          <base-icon
            position="left"
            iconRef="save"
          />Advies opslaan</base-button>
      </div>
      <div class="scenario-tabs page-break-before">
        <button
          class="button secondary tab"
          :class="{ active: scenario.active }"
          v-for="(scenario, scenarioIndex) in scenarios"
          :key="scenarioIndex"
          @click.self="setActiveScenario(scenarioIndex)"
        >
          <span @click.self="setActiveScenario(scenarioIndex)">{{scenarios[scenarioIndex].label}}</span>
          <base-button
            v-show="scenario.active"
            type="secondary"
            class="inline-button"
            @click="editScenarioLabel(scenarioIndex)"
          >
            <base-icon
              iconRef="edit-2"
              position="right"
            />
          </base-button>
          <base-button
            v-show="scenario.active"
            type="secondary"
            class="inline-button"
            @click="deleteScenario(scenarioIndex)"
          >
            <base-icon
              iconRef="close"
              position="right"
            />
          </base-button>
        </button>
        <base-button
          type="secondary"
          @click="addScenario"
        >
          <base-icon iconRef="plus" />
        </base-button>
      </div>
      <!-- Producten -->
      <div class="advice-product-cards-wrapper">
        <div
          class="advice-product-cards"
          :class="{active: scenario.active}"
          v-for="(scenario, scenarioIndex) in scenarios"
          :key="scenarioIndex"
          v-show="scenario.active"
        >
          <!-- Product card -->
          <h3 class="show-only-on-print page-break-before">{{scenario.label}}</h3>
          <div
            class="add-people-button-wrapper"
            v-show="scenario.hasPeople != true"
          >
            <base-button
              type="secondary"
              @click="addPeople(scenarioIndex)"
            >
              <base-icon
                iconRef="plus"
                position="left"
              />Personen toevoegen
            </base-button>
          </div>
          <div
            class="advice-people-card"
            v-if="scenario.hasPeople == true"
          >
            <div class="value-row">
              <div class="value-group">
                <label
                  for=""
                  class="label"
                >Aantal personen gemiddeld aanwezig</label>
                <input
                  type="number"
                  name=""
                  id=""
                  class="input"
                  v-model="scenario.peopleAmount"
                >
                <div class="value show-only-on-print">{{scenario.peopleAmount}}</div>
              </div>
              <div class="value-group">
                <label
                  for=""
                  class="label"
                >Gedrag personen</label>
                <select
                  class="input"
                  v-bind:value="scenario.peopleType"
                  @change="scenario.peopleType = $event.target.value"
                >
                  <option
                    :value="peopleType.value"
                    v-for="peopleType in $peopleTypes"
                    :key="peopleType.value"
                  >{{peopleType.label}}</option>
                </select>
                <div class="value show-only-on-print">{{$peopleTypes.find(peopleType => peopleType.value == scenario.peopleType).label}}</div>
              </div>
            </div>
          </div>
          <div
            class="advice-product-card"
            v-for="(product, index) in scenarios[scenarioIndex].addedProducts"
            :key="index"
            :class="{editing : product.editing}"
          >
            <div class="advice-product-card-half advice-product-card-top-half">
              <!-- Product Image -->
              <div class="advice-product-card-image">
                <img
                  :src="product.imgUrl"
                  alt="Rivasono designpaneel"
                >
              </div>
              <!-- Product Category -->
              <div class="advice-product-card-content">
                <div class="value-group product-category">
                  <div
                    class="value"
                    v-show="!product.editing"
                  >
                    {{product.category}}
                  </div>
                </div>
                <!-- Product Name -->
                <div class="value-group product-name">
                  <div
                    class="value"
                    v-show="!product.editing"
                  >
                    {{product.productName}}
                  </div>
                  <b-field
                    label="Producten"
                    v-show="product.editing"
                  >
                    <b-autocomplete
                      v-model="product.productName"
                      placeholder="Kies een product..."
                      :open-on-focus="true"
                      :data="filterProductsListOn(product.productName)"
                      field="productName"
                      class="product-autocomplete"
                      @input="changed(scenarioIndex, index); calculateTotalArea(scenarioIndex, index);"
                    >
                    </b-autocomplete>
                  </b-field>
                </div>
                <!-- Product Description -->
                <div class="value-row">
                  <div class="value-group product-description">
                    <div
                      class="value"
                      v-show="!product.editing"
                    >
                      {{product.description}}
                    </div>
                  </div>
                </div>
                <!-- Product Sizes, amount and note -->
                <div
                  class="value-row"
                  v-if="getWeightedAverage(product.ac) != 0"
                >
                  <div class="value-group product-size">
                    <label class="label">Breedte, hoogte en opp.</label>
                    <div>
                      <span
                        class="value"
                        v-show="!product.editing || product.editing && product.hasFixedSize"
                      >{{product.height}}</span>
                      <input
                        type="number"
                        class="input value"
                        placeholder="0"
                        v-model="product.height"
                        @keyup="calculateArea(scenarioIndex, index); calculateTotalArea(scenarioIndex, index)"
                        v-show="product.editing && !product.hasFixedSize"
                      >
                      <span>m&nbsp;x&nbsp;</span>
                      <span
                        class="value"
                        v-show="!product.editing || product.editing && product.hasFixedSize"
                      >{{product.width}}</span>
                      <input
                        type="number"
                        class="input value"
                        placeholder="0"
                        v-model="product.width"
                        @keyup="calculateArea(scenarioIndex, index); calculateTotalArea(scenarioIndex, index)"
                        v-show="product.editing && !product.hasFixedSize"
                      >
                      <span>m&nbsp;=&nbsp;</span>
                      <span
                        class="value"
                        v-show="!product.editing || product.editing && product.hasFixedSize"
                      >{{product.area}}</span>
                      <input
                        type="number"
                        class="input value"
                        placeholder="0"
                        v-model="product.area"
                        @keyup="calculateTotalArea(scenarioIndex, index)"
                        v-show="product.editing && !product.hasFixedSize"
                      >
                      <span>m<sup>2</sup></span>
                    </div>
                  </div>
                  <div class="value-group product-distance">
                    <label class="label">Afstand</label>
                    <div
                      class="value"
                      v-show="!product.editing"
                    >{{product.distanceLabel}}</div>
                    <select
                      class="input value"
                      v-bind:value="product.distance"
                      @change="product.distance = $event.target.value; product.distanceLabel = $event.target.options[$event.target.selectedIndex].text"
                      v-show="product.editing"
                    >
                      <option value="1">Gemiddeld (2m tot 4m)</option>
                      <option value="0.9">Dichtbij (&lt; 2m)</option>
                      <option value="1.2">Op afstand (4m tot 8m)</option>
                      <option value="1.5">Op grote afstand (&gt; 8m)</option>
                    </select>
                  </div>
                </div>
                <div class="value-row">
                  <div class="value-group product-amount">
                    <label class="label">Aantal
                    </label>
                    <div class="flex-row">
                      <base-button
                        type="secondary"
                        class="inline-button left"
                        @click=" product.amount++; calculateTotalArea(scenarioIndex, index)"
                      >
                        <base-icon iconRef="plus" />
                      </base-button>
                      <base-button
                        type="secondary"
                        class="inline-button left"
                        @click="decreaseProductAmount(product); calculateTotalArea(scenarioIndex, index)"
                      >
                        <base-icon iconRef="minus" />
                      </base-button>
                      <input
                        type="number"
                        class="input value"
                        placeholder="0"
                        @keyup="calculateTotalArea(scenarioIndex, index)"
                        v-model="product.amount"
                        v-show="product.editing"
                      >
                      <div v-show="!product.editing"><span class="value">{{product.amount}}</span> stuks</div>
                    </div>
                  </div>
                  <div
                    class="value-group product-totalarea"
                    v-if="getWeightedAverage(product.ac) != 0"
                  >
                    <label class="label">Opp. totaal</label>
                    <div class="value">{{product.totalArea}} m<sup>2</sup></div>
                  </div>
                </div>
                <div
                  class="value-row"
                  v-if="product.editing || product.note != ''"
                >
                  <div class="value-group product-note">
                    <label class="label">Opmerking</label>
                    <div
                      class="value"
                      v-show="!product.editing"
                    >{{product.note}}</div>
                    <textarea
                      class="input value"
                      v-model="product.note"
                      v-show="product.editing"
                    ></textarea>
                  </div>
                </div>
              </div>
              <!-- Card Buttons -->
              <div class="product-card-buttons">
                <base-button
                  type="secondary"
                  @click="toggleEdit(scenarioIndex, index)"
                  v-show="!product.editing"
                >
                  <base-icon iconRef="edit" />
                </base-button>
                <base-button
                  type="secondary"
                  @click="duplicateProduct(scenarioIndex, index)"
                  v-show="!product.editing"
                >
                  <base-icon iconRef="copy" />
                </base-button>
                <base-button
                  type="primary"
                  @click="toggleEdit(scenarioIndex, index)"
                  v-show="product.editing"
                >
                  <base-icon iconRef="checkmark" />
                </base-button>
                <base-button
                  type="danger"
                  @click="deleteProduct(scenarioIndex, index)"
                  v-show="product.editing"
                >
                  <base-icon iconRef="trash" />
                </base-button>
              </div>
            </div>
          </div>
          <div
            class="flex show-only-on-print"
            v-if="scenarios[scenarioIndex].addedProducts.length === 0"
          >
            <div class="flex-divider"></div>
            <p>Geen producten toegevoegd</p>
            <div class="flex-divider"></div>
          </div>
          <div class="add-product-button-wrapper">
            <base-button
              type="secondary"
              class="add-product-button"
              @click="addProduct(scenarioIndex)"
            >
              <base-icon
                iconRef="plus"
                position="left"
              />Product toevoegen
            </base-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MeasurementsChart from "../chart-components/measurements-chart";
import AcousticsscoreChart from "../chart-components/acousticsscore-chart";

import _ from "lodash/core";

export default {
  components: {
    MeasurementsChart,
    AcousticsscoreChart
  },
  data: function() {
    return {
      test: "",
      testSelection: "",
      constant: 0.161,
      correction: 1,
      savingAdvice: false,
      scenarios: [
        {
          active: true,
          editingLabel: false,
          label: "Scenario 1",
          addedProducts: []
        }
      ]
    };
  },
  watch: {
    currentRoom: function() {
      if (this.currentRoom) this.loadAdviceIfRoomHasOne();
    }
  },
  computed: {
    allRoomTypeOptions: function() {
      return this.$store.getters.getAdvicevaluesList;
    },
    currentRoomType: function() {
      return this.allRoomTypeOptions.find(
        roomType => roomType.id == this.currentRoom.data().type
      );
    },
    currentRoom: function() {
      return this.$store.getters.getCurrentClientPage.room;
    },
    currentRoomMeasurements: function() {
      return this.$store.getters.getMeasurementsList.filter(measurement => {
        return (
          measurement.data().belongsTo == this.currentRoom.id &&
          measurement.data().trashed == false
        );
      });
    },
    currentRoomArea: function() {
      return this.currentRoom.data().width * this.currentRoom.data().length;
    },
    currentRoomVolume: function() {
      return this.currentRoomArea * this.currentRoom.data().height;
    },
    currentProject: function() {
      return this.$store.getters.getProjectsList.find(project => {
        return project.id == this.currentRoom.data().belongsTo;
      });
    },
    currentClient: function() {
      return this.$store.getters.getClientsList.find(client => {
        return client.id == this.currentProject.data().belongsTo;
      });
    },
    productsList: function() {
      return this.$store.getters.getProductsList;
    },
    filteredProductsList: function() {
      const filteredProductsList = [];
      this.productsList.forEach(product => {
        filteredProductsList.push({
          active: product.field_364,
          productName: product.field_125,
          brand: product.field_126,
          sku: product.field_127,
          category: product.field_365,
          height: product.field_374_raw,
          width: product.field_375_raw,
          ac: [
            product.field_368_raw,
            product.field_369_raw,
            product.field_370_raw,
            product.field_371_raw,
            product.field_372_raw,
            product.field_373_raw
          ],
          description: product.field_366,
          priceEach: product.field_378_raw,
          pricePerM2: product.field_384_raw,
          imgUrl: product.field_385_raw.url
        });
      });
      return this.$sortProductsAlphabetically(
        filteredProductsList,
        "productName"
      );
    },
    uniqueProductCategories: function() {
      return [...new Set(this.filteredProductsList.map(item => item.category))];
    },
    filteredProductsSortedByCategory: function() {
      const filteredProductsSortedByCategory = [];
      this.uniqueProductCategories.forEach((category, categoryIndex) => {
        filteredProductsSortedByCategory.push(
          this.filteredProductsList.filter(product => {
            return (
              product.category == this.uniqueProductCategories[categoryIndex]
            );
          })
        );
      });
      return filteredProductsSortedByCategory;
    },
    zeroMeasurement: function() {
      return this.currentRoomMeasurements.find(measurement => {
        return measurement.data().type == "Nulmeting";
      });
    },
    currentRoomTypeAdviceValues: function() {
      if (this.currentRoomType.values[0].volume == 0) {
        return this.currentRoomType.values
          .slice()
          .reverse()
          .find(value => {
            return this.currentRoomVolume > value.volume;
          }).rt60;
      } else {
        return this.currentRoomType.values.find(value => {
          return this.currentRoomVolume > value.volume;
        }).rt60;
      }
    },
    rt60ContextualAdviceValues: function() {
      const array = [];
      this.currentRoomTypeAdviceValues.forEach(value => {
        array.push(
          value *
            this.currentRoom.data().interior *
            this.currentRoom.data().wishes *
            this.correction
        );
      });
      return array;
    },
    rt60ZeroMeasurement: function() {
      return [
        this.zeroMeasurement.data().hz125,
        this.zeroMeasurement.data().hz250,
        this.zeroMeasurement.data().hz500,
        this.zeroMeasurement.data().hz1000,
        this.zeroMeasurement.data().hz2000,
        this.zeroMeasurement.data().hz4000
      ];
    },
    zeroMeasurementAbsorbtion: function() {
      const array = [];
      this.rt60ZeroMeasurement.forEach(value => {
        array.push((this.constant * this.currentRoomVolume) / value);
      });
      return array;
    },
    productAbsorbtion: function() {
      const array = [0, 0, 0, 0, 0, 0];
      if (this.currentScenario.hasPeople) {
        const peopleAmount = this.currentScenario.peopleAmount;
        const peopleTypeSabins = this.$peopleTypes.find(
          peopleType => peopleType.value == this.currentScenario.peopleType
        ).sabins;
        for (let i = 0; i < 6; i++) {
          array[i] += peopleAmount * peopleTypeSabins[i];
        }
      }
      this.currentScenario.addedProducts.forEach(product => {
        for (let i = 0; i < 6; i++) {
          array[i] += (product.ac[i] * product.totalArea) / product.distance;
        }
      });
      return array;
    },
    rt60WithProducts: function() {
      const totalAbsorbtion = [];
      for (let i = 0; i < 6; i++) {
        totalAbsorbtion[i] =
          this.zeroMeasurementAbsorbtion[i] + this.productAbsorbtion[i];
      }
      const array = [];
      totalAbsorbtion.forEach(value => {
        array.push((this.currentRoomVolume * this.constant) / value);
      });
      return array;
    },
    chartMax: function() {
      const measurementsMax = Math.ceil(
        Math.max.apply(Math, this.rt60ZeroMeasurement)
      );
      const adviceValuesMax = Math.ceil(
        Math.max.apply(Math, this.rt60ContextualAdviceValues)
      );
      if (measurementsMax > adviceValuesMax) {
        return measurementsMax + 0.2;
      } else {
        return adviceValuesMax + 0.2;
      }
    },
    acousticsScores: function() {
      const percentages = [
        (this.getWeightedAverage(this.rt60ContextualAdviceValues) /
          this.getWeightedAverage(this.rt60ZeroMeasurement)) *
          100,
        (this.getWeightedAverage(this.rt60ContextualAdviceValues) /
          this.getWeightedAverage(this.rt60WithProducts)) *
          100
      ];
      const scores = [
        (Math.round(percentages[0]) / 10).toFixed(1),
        (Math.round(percentages[1]) / 10).toFixed(1)
      ];
      if (scores[1] >= 10) {
        scores[1] = 10;
      }
      return scores;
    },
    currentScenario: function() {
      return this.scenarios.find(scenario => {
        return scenario.active;
      });
    },
    currentScenarioHasNoProducts: function() {
      return this.currentScenario.addedProducts.length == 0;
    },
    currentScenarioProductsTotalArea: function() {
      let value = 0;
      this.currentScenario.addedProducts.forEach(product => {
        value += product.totalArea;
      });
      return value;
    },
    adviceChanged: function() {
      return !_.isEqual(this.scenarios, this.currentRoom.data().scenarios);
    }
  },
  methods: {
    addPeople(scenarioIndex) {
      this.$set(this.scenarios[scenarioIndex], "hasPeople", true);
      this.$set(this.scenarios[scenarioIndex], "peopleAmount", 0);
      this.$set(this.scenarios[scenarioIndex], "peopleType", 0);
    },
    openCorrectionPrompt: function() {
      this.$dialog.prompt({
        message: "Correctiefactor:",
        inputAttrs: {
          type: "number",
          value: this.correction,
          min: 0,
          step: "0.01"
        },
        cancelText: "Annuleer",
        confirmText: "Opslaan",
        onConfirm: value => {
          this.correction = value;
          this.saveAdvice();
        }
      });
    },
    printAdvice: function() {
      window.print();
    },
    saveAdvice: function() {
      this.savingAdvice = true;
      this.$firestore
        .collection("rooms")
        .doc(this.currentRoom.id)
        .set(
          {
            scenarios: this.scenarios,
            hasAdvice: true,
            correction: this.correction
          },
          { merge: true }
        )
        .then(() => {
          this.$store.dispatch("fetchRooms").then(() => {
            this.savingAdvice = false;
          });
        })
        .catch(error => {
          this.savingAdvice = false;
          console.log("Error saving advice: ", error);
        });
    },
    toggleEdit: function(scenarioIndex, productIndex) {
      this.scenarios[scenarioIndex].addedProducts[productIndex].editing = !this
        .scenarios[scenarioIndex].addedProducts[productIndex].editing;
    },
    changed: function(scenarioIndex, productIndex) {
      const currentProduct = this.scenarios[scenarioIndex].addedProducts[
        productIndex
      ];
      const selectedProduct = this.filteredProductsList.find(product => {
        return product.productName == currentProduct.productName;
      });
      if (selectedProduct) {
        currentProduct.productName = selectedProduct.productName;
        currentProduct.description = selectedProduct.description;
        currentProduct.category = selectedProduct.category;
        currentProduct.sku = selectedProduct.sku;
        currentProduct.width = selectedProduct.width / 100;
        currentProduct.height = selectedProduct.height / 100;
        currentProduct.area = selectedProduct.area;
        currentProduct.ac = selectedProduct.ac;
        currentProduct.imgUrl = selectedProduct.imgUrl;
        if (selectedProduct.width != 0 && selectedProduct.height != 0) {
          currentProduct.hasFixedSize = true;
          currentProduct.area = currentProduct.width * currentProduct.height;
        } else {
          currentProduct.hasFixedSize = false;
          currentProduct.area = 0;
        }
        if (
          selectedProduct.ac.every(value => {
            return value == 0;
          })
        ) {
          currentProduct.nonAcoustic = true;
        } else {
          currentProduct.nonAcoustic = false;
        }
        if (selectedProduct.imgUrl == undefined) {
          currentProduct.imgUrl =
            "https://dummyimage.com/480x270/ffffff/009ad9.png&text=++Rivasono++";
        }
      }
    },
    decreaseProductAmount: function(product) {
      if (product.amount > 0) product.amount--;
    },
    calculateArea: function(scenarioIndex, productIndex) {
      const currentProduct = this.scenarios[scenarioIndex].addedProducts[
        productIndex
      ];
      currentProduct.area = this.roundTo(
        Number(currentProduct.height) * Number(currentProduct.width),
        2
      );
    },
    calculateTotalArea: function(scenarioIndex, productIndex) {
      const currentProduct = this.scenarios[scenarioIndex].addedProducts[
        productIndex
      ];
      currentProduct.totalArea = this.roundTo(
        Number(currentProduct.area) * Number(currentProduct.amount),
        2
      );
    },
    addScenario: function() {
      this.scenarios.push({
        active: false,
        label: `Scenario ${this.scenarios.length + 1}`,
        editingLabel: false,
        addedProducts: []
      });
      this.setActiveScenario(this.scenarios.length - 1);
    },
    editScenarioLabel: function(scenarioIndex) {
      this.$dialog.prompt({
        message: "Verander scenario label",
        inputAttrs: {
          type: "text",
          value: this.scenarios[scenarioIndex].label
        },
        cancelText: "Annuleren",
        confirmText: "Opslaan",
        onConfirm: value => {
          this.scenarios[scenarioIndex].label = value;
          this.saveAdvice();
        }
      });
    },
    deleteScenario: function(scenarioIndex) {
      if (this.scenarios.length == 1) return;
      if (scenarioIndex == 0) {
        this.scenarios[1].active = true;
      } else if (scenarioIndex >= 1) {
        this.scenarios[scenarioIndex - 1].active = true;
      }
      this.scenarios.splice(scenarioIndex, 1);
    },
    setActiveScenario: function(scenarioIndex) {
      this.scenarios.forEach(scenario => {
        scenario.active = false;
      });
      this.scenarios[scenarioIndex].active = true;
    },
    addProduct: function(scenarioIndex) {
      this.scenarios[scenarioIndex].addedProducts.push({
        category: "Kies een productcategorie...",
        productName: "",
        sku: "0",
        ac: [0, 0, 0, 0, 0, 0],
        height: "",
        width: "",
        area: "",
        amount: "0",
        distance: 1,
        distanceLabel: "Gemiddeld (2m tot 4m)",
        totalArea: 0,
        note: "",
        hasFixedSize: false,
        imgUrl:
          "https://dummyimage.com/480x270/ffffff/333333.png&text=++afbeelding++",
        editing: true
      });
    },
    deleteProduct: function(scenarioIndex, productIndex) {
      this.scenarios[scenarioIndex].addedProducts.splice(productIndex, 1);
    },
    duplicateProduct: function(scenarioIndex, productIndex) {
      const productToDuplicate = Object.assign(
        {},
        this.scenarios[scenarioIndex].addedProducts[productIndex]
      );
      this.scenarios[scenarioIndex].addedProducts.splice(
        productIndex + 1,
        0,
        productToDuplicate
      );
    },
    loadAdviceIfRoomHasOne: function() {
      if (this.currentRoom.data().hasAdvice === true) {
        this.scenarios = this.currentRoom.data().scenarios;
        this.correction = this.currentRoom.data().correction;
      }
    },
    getWeightedAverage: function(array) {
      let total = 0;
      for (let i = 0; i < 6; i++) {
        total += array[i];
      }
      for (let i = 1; i < 5; i++) {
        total += array[i];
      }
      return total / 10;
    },
    roundTo: function(value, sig) {
      return Number(value.toFixed(sig));
    },
    prettifyDate: function(milliseconds) {
      const currentDate = new Date(1970, 0, 1);
      currentDate.setSeconds(milliseconds / 1000);
      const months = [
        "januari",
        "februari",
        "maart",
        "april",
        "mei",
        "juni",
        "juli",
        "augustus",
        "september",
        "iktober",
        "november",
        "december"
      ];
      const day = currentDate.getDate();
      const monthIndex = currentDate.getMonth();
      const year = currentDate.getFullYear();
      return `${day} ${months[monthIndex]} ${year}`;
    },
    filterProductsListOn: function(value) {
      return this.filteredProductsList.filter(product => {
        return product.productName.toLowerCase().includes(value.toLowerCase());
      });
    }
  },
  mounted: function() {
    if (this.currentRoom) this.loadAdviceIfRoomHasOne();
  }
};
</script>


