<template>
  <div class="acousticsscore-chart">
    <div class="acousticsscore-wrapper">
      <h4>Rivasono Akoestiek Score</h4>
      <div class="scores">
        <div class="score-row">
          <div class="score-label">Huidige akoestiek</div>
          <div class="score-value">
            <span class="score current">
              <base-counter
                :end="acousticsScore"
                :decimals="1"
                :max="10"
                :noDecimalsIfAtMax="true"
              />
            </span> /
            <span class="score-affix advice">10</span>
          </div>
        </div>
        <div
          class="score-row"
          v-show="!currentScenarioHasNoProducts"
        >
          <div class="score-label">Na toepassen producten</div>
          <div class="score-value">
            <span class="score after">
              <base-counter
                :end="acousticsScoreWithProducts"
                :decimals="1"
                :max="10"
                :noDecimalsIfAtMax="true"
              />
            </span> /
            <span class="advice">10</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["acousticsScores", "currentScenarioHasNoProducts"],
  computed: {
    acousticsScore: function() {
      return this.acousticsScores[0];
    },
    acousticsScoreWithProducts: function() {
      return this.acousticsScores[1];
    }
  }
};
</script>



